import { Component, OnInit } from '@angular/core';
import { MessageServiceService } from '../../services/message-service.service';

import Swal from 'sweetalert2';

@Component({
  selector: 'app-honey',
  templateUrl: './honey.component.html',
  styleUrls: ['./honey.component.css']
})
export class HoneyComponent implements OnInit {

  constructor(public _MessageService: MessageServiceService) { }

  contactForm(form) {
    this._MessageService.sendMessage(form).subscribe((data) => {
      Swal.fire(
        "Formulario de contacto",
        "Mensaje enviado correctamente",
        "success"
      );

      
    });

    
  }

  reloadPage(){
    location.reload();
  }

  ngOnInit(): void {
  }

}
